.journal {
    display: grid;
    gap: 1rem;
    grid-template-columns: 5fr 2fr;
}

.articles>div {
    margin-bottom: 2rem;
    border-bottom: 1px solid rgba(17, 17, 17, 0.15);
}

.articles :last-child {
    border-bottom: transparent;
}


@media screen and (max-width: 767px) {
    .journal{
        grid-template-columns: 1fr;
    }
}