.article{
    max-width: 845px;
}

.article > div{
    margin-bottom: 1rem;
}

.article-cover{
    width: 100%;
    border-radius: 8px;
}

.article-writer{
    display: flex;
    justify-content: space-between;
    align-items: center;
}

.article .writer-info{
    display: grid;
    gap: 1.5rem;
    grid-template-columns: 1fr 2fr;
}

.article button{
    color: #6047EC;
    border: none;
    background-color: transparent;
    text-decoration: underline;
    font-size: 1rem;
    cursor: pointer;
}