.bookmarks-wrapper{
    position: sticky;
    top: 10px;
}

.read-time-div{
    border: 2px solid #6047EC;
    border-radius: 8px;
    padding: 1rem 1.5rem;
    color: #6047EC;
    margin-bottom: 2rem;
}

.bookmarks{
    background-color: rgba(17, 17, 17, 0.05);
    border-radius: 8px;
    padding: 1rem;
}

.bookmarks p{
    padding: .8rem 1rem;
    background-color: white;
    border-radius: 8px;
    margin-top: 1rem;
}

